.header__title {
    display: inline-block;
    width: 100%;
    font-weight: bold;
    font-size: 48px;
    line-height: 1.2;
    color: #fff;
    text-align: center;
    text-shadow: 0 4px rgba(0, 0, 0, 0.25);
}

.title__main {
    margin-top: 16%;
}

.title__secondary {
    margin-top: 11%;
}

@media (max-width: 1439px) {
    .header__title {
        font-size: 40px;
    }
    .title__main {
        margin-top: 181px;
    }
    .title__secondary {
        margin-top: 130px;
    }
}

@media (max-width: 991px) {
 
}

@media (max-width: 767px) {
    .header__title {
        font-size: 36px;
    }
}
@media (max-width: 575px) {
    .header__title {
        font-size: 32px;
    }
}