.button {
  display: block;
  height: 40px;
  width: 140px;
  font-size: 16px;
  font-weight: bold;
  line-height: 38px;
  text-align: center;
  border: 1px solid #000;
  border-radius: 4px;
  background-color: transparent;
  margin: 0 auto;
  box-shadow: 0 2px rgba(0, 0, 0, 0.25);
}
.button p {
  display: block;
  width: 100%;
  height: 100%;
  text-decoration: none;
  color: #000;
  text-shadow: 0 2px rgba(0, 0, 0, 0.25);
}

@media (max-width: 1439px) {
  .button {
    height: 30px;
    width: 120px;
    font-size: 14px;
    line-height: 28px;
    margin-top: 30px;
  }
}/*# sourceMappingURL=Button.css.map */