.cards_block {
    max-width: 870px;
    margin: 50px auto 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

@media (max-width: 1439px) {
  
}

@media (max-width: 991px) {
    .cards_block {
        max-width: 582px;
    }
}

@media (max-width: 767px) {
    .cards_block {
        max-width: 576px
    }
}

@media (max-width: 575px) {
    .cards_block {
        width: 90%; 
    }
}
