.header__main {
  position: relative;
  width: 100%;
  height: 44.44vw;
  min-height: 640px;
  background: url("../../assets/img/main_header_bg.jpg") center center no-repeat;
  background-size: cover;
}
.header__container {
  width: 80vw;
  margin: 0 auto;
}
.header__button {
  display: block;
  height: 40px;
  width: 140px;
  font-size: 16px;
  font-weight: bold;
  line-height: 38px;
  text-align: center;
  border: 1px solid #fff;
  border-radius: 4px;
  background-color: transparent;
  margin: 35px auto 0;
  box-shadow: 0 2px rgba(0, 0, 0, 0.25);
}
.header__button__link {
  display: block;
  width: 100%;
  height: 100%;
  text-decoration: none;
  color: #fff;
  text-shadow: 0 2px rgba(0, 0, 0, 0.25);
}

@media (max-width: 1439px) {
  .header__button {
    height: 30px;
    width: 120px;
    font-size: 14px;
    line-height: 28px;
    margin-top: 30px;
  }
}
@media (max-width: 767px) {
  .header {
    background-position-x: 75%;
  }
}/*# sourceMappingURL=HeaderMain.css.map */